<template>
</div>
  <map
    :center.sync="center"
    :zoom.sync="zoom"
    :map-type-id.sync="mapType"
    @g-click="mapClick()"
    >
    <marker
      v-if="showIcon"
      :position.sync="Icon"
      :icon.sync="icon"
      @g-click="center = LA"
    ></marker>
    <marker
      v-if="mkvisible"
      :position.sync="mposition"
      @g-click="center = mposition"
    ></marker>
    <marker
      v-if="LAvisible"
      :position.sync="LA"
      @g-click="center = LA"
    ></marker>
    <marker
      v-if="Fvisible"
      :position.sync="Fairbanks"
      @g-click="center = Fairbanks"
    ></marker>

    <info-window
    v-if="infowin"
    :position.sync="infoWP"
    :opened.sync="infowin"
    >
      {{{info}}}
    </info-window>

    <polyline v-if="plvisible" :path.sync="plPath" :options="{geodesic:true, strokeColor:'#FF0000'}">
    </polyline>
    
  </map>

</div>
</template>

<script>
import {load, Marker, Map, Cluster, InfoWindow, Polyline, Rectangle, Circle, Polygon, PlaceInput} from 'vue-google-maps'

load('AIzaSyDvw8wuIuika2GitdeO-qJJkEKDe3s32I0', '', ['places'], true)

export default {
  data: function data () {
    return {
      info: '',
      showIcon: false,
      mkvisible: false,
      LAvisible: false,
      Fvisible: false,
      infoWin: '',
      infowin: false,
      infoWP: {lat: 23.3959079000, lng: 113.3079699000},
      icon: {url: 'http://www.netnook.cn/static/loveyy/airplane.png'},
      plane: require('../assets/plane.png'),
      jingdu: {lat: 35.0054841000, lng: 135.7773509000},
      yishishi: {lat: 34.4875144000, lng: 136.7093359000},
      kending: {lat: 21.9483307000, lng: 120.7797516000},
      hualian: {lat: 23.9871589000, lng: 121.6015714000},
      jiufen: {lat: 25.1091870000, lng: 121.8462979000},
      ximending: {lat: 25.0456572000, lng: 121.5077373000},
      taibei: {lat: 25.0329694000, lng: 121.5654177000},
      mangu: {lat: 13.7563309000, lng: 100.5017651000},
      qingmai: {lat: 18.7060641000, lng: 98.9817163000},
      test1: '',
      infoWinP: {lat: 23.3959079000, lng: 113.3079699000},
      mposition: {lat: 23.3959079000, lng: 113.3079699000},
      Foshan: {lat: 23.0471681521, lng: 113.1254234346},
      center: {lat: 23.3959079000, lng: 113.3079699000},
      Icon: {lat: 23.0471681521, lng: 113.1254234346},
      line: {
        guangzhou: {lat: 23.3959079000, lng: 113.3079699000},
        jingdu: {lat: 35.0054841000, lng: 135.7773509000},
        yishishi: {lat: 34.4875144000, lng: 136.7093359000},
        kending: {lat: 21.9483307000, lng: 120.7797516000},
        hualian: {lat: 23.9871589000, lng: 121.6015714000},
        jiufen: {lat: 25.1091870000, lng: 121.8462979000},
        ximending: {lat: 25.0456572000, lng: 121.5077373000},
        taibei: {lat: 25.0329694000, lng: 121.5654177000},
        mangu: {lat: 13.7563309000, lng: 100.5017651000},
        qingmai: {lat: 18.7060641000, lng: 98.9817163000}
      },
      taiguo: {
        mangu: {lat: 13.7563309000, lng: 100.5017651000},
        qingmai: {lat: 18.7060641000, lng: 98.9817163000}
      },
      carLoad: [
        {lat: 23.3959079000, lng: 113.3079699000},
        {lat: 35.0054841000, lng: 135.7773509000},
        {lat: 34.4875144000, lng: 136.7093359000},
        {lat: 21.9483307000, lng: 120.7797516000},
        {lat: 23.9871589000, lng: 121.6015714000},
        {lat: 25.1091870000, lng: 121.8462979000},
        {lat: 25.0456572000, lng: 121.5077373000},
        {lat: 25.0329694000, lng: 121.5654177000},
        {lat: 13.7563309000, lng: 100.5017651000},
        {lat: 18.7060641000, lng: 98.9817163000}
      ],
      zoom: 0,
      gridSize: 50,
      mapType: 'roadmap', // ROADMAP
      markers: [
        [{
          position: {lat: 23.0471681521, lng: 113.1254234346}
        }, {
          position: {lat: 23.40085, lng: 113.312942}
        }]
      ],
      markersEven: false,
      // mapStyle: 'green',
      plPath: [
        {lat: 23.0471681521, lng: 113.1254234346},
        {lat: 33.9415889000, lng: -118.4085300000}
        // {lat: 64.8377360070, lng: -147.7159881592}
      ],
      plvisible: false,
      pictureHtml: [
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/baiyun.png">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/japan/jingdu.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/japan/yishi.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/taiwan/kending.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/taiwan/hualian.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/taiwan/jiufen.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/taiwan/ximending.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/taiwan/taibei.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/taiguo/mangu.jpg">',
        '<br/><img style="width:100%" src="http://120.77.183.219/pic/taiguo/qingmai.jpg">'
      ],
      carPic: [
        {lat: 67.5144121170, lng: -149.8503398895},
        {lat: 67.6305279667, lng: -149.7760963440},
        {lat: 67.7475050041, lng: -149.7603034970},
        {lat: 67.8765434167, lng: -149.8227024078},
        {lat: 67.9801374511, lng: -149.7636508942},
        {lat: 68.0899651872, lng: -149.5508766174},
        {lat: 68.1963710980, lng: -149.4152641296},
        {lat: 68.3133255367, lng: -149.3550109863},
        {lat: 68.3464141078, lng: -149.3381881701},
        {lat: 68.6055152941, lng: -149.5362854004}
      ],
      infoPic: [
        '',
        '秋色，京都的秋',
        '伊势神宫，的旁边',
        '一半好，一半坏的垦丁',
        '花莲，逛夜市，晚上就逛夜市',
        '九份的夜，有很多灯笼',
        '西门町,车水马龙',
        '台北，you have saw',
        '十月的曼谷，你将要去',
        '十月的清迈，你将要去，但是...'
      ]
    }
  },

  filters: {
    markerRemover (markers) {
      if (this.markersEven) {
        const result = []
        for (let i = 0; i < markers.length; i += 2) {
          result.push(markers[i])
        }
        return result
      } else {
        return markers
      }
    }
  },
  computed: {
    mapStyles () {
      switch (this.mapStyle) {
        case 'normal':
          return []
        case 'red':
          return [{stylers: [{hue: '#890000'}, {visibility: 'simplified'}, {gamma: 0.5}, {weight: 0.5}]},
          {elementType: 'labels', stylers: [{visibility: 'off'}]},
          {featureType: 'water', stylers: [{color: '#890000'}]}]
        default:
          return [{stylers: [{hue: '#899999'}, {visibility: 'on'}, {gamma: 0.5}, {weight: 0.5}]},
              {featureType: 'road', stylers: [{visibility: 'off'}]},
              {featureType: 'transit.line', stylers: [{color: '#FF0000'}]},
              {featureType: 'poi', elementType: 'labels.icon', stylers: [{visibility: 'on'}, {weight: 10}]},
              {featureType: 'water', stylers: [{color: '#8900FF'}, {weight: 9999900000}]}]
      }
    }
  },
  ready () {
    this.zoomTo(12, 1000, this.toGuangzhou)
  },
  methods: {
    zoomTo (zoom, time, callback, point) {
      // let map = {}
      time = time || 1000
      let loop = null
      let self = this
      let loopZoom = function () {
        let curZoom = self.zoom
        if (curZoom === zoom) {
          clearTimeout(loop)
          callback = callback || function () {}
          callback()
          return false
        }
        self.mkvisible = false
        self.mkvisible = true
        let plus = curZoom > zoom ? -1 : 1
        let toZoom = curZoom + plus
        // map.setZoom(toZoom)
        self.zoom = toZoom
        if (point) {
          self.center = point
        }
        loop = setTimeout(loopZoom, time)
      }
      loopZoom()
    },
    toGuangzhou (callback) {
      let self = this
      this.infowin = true
      // this.center =
      this.info = 'Let \'s go? I want to take you see the world you saw and will see.'
      // to world
      setTimeout(function () {
        self.infowin = false
        callback = callback || function () {}
        callback()
        self.flyWorld()
      }, 8000)
    },
    flyWorld (time, callback, point) {
      time = time || 6000
      let loop = null
      let length = this.pictureHtml.length
      let i = 0
      let self = this
      self.zoom = 12
      this.icon = {url: 'http://www.netnook.cn/static/loveyy/car.png'}
      let loopPic = function () {
        if (i === length) {
          clearTimeout(loop)
          callback = callback || function () {}
          callback()
          setTimeout(function () {
            self.all()
          }, 6000)
          return false
        }
        self.center = self.carLoad[i]
        self.infoWP = self.carLoad[i]
        self.Icon = self.carPic[i]
        self.info = self.pictureHtml[i] + self.infoPic[i]
        self.infowin = true
        i++
        if (point) {
          self.center = point
        }
        loop = setTimeout(loopPic, time)
      }
      loopPic()
    },
    all () {
      this.$dispatch('all')
    }
  },
  components: {
    Map,
    Marker,
    Cluster,
    InfoWindow,
    Polygon,
    Polyline,
    Rectangle,
    Circle,
    PlaceInput
  }
}
</script>

<style>
map {
  width:100%;
  height: 600px;
  display: block;
}
</style>



